import React from 'react';
import { Link } from 'gatsby';

import Layout from '../components/layout';
import SEO from '../components/seo';
import { useStaticQuery, graphql } from 'gatsby';
import Container from 'react-bootstrap/Container';

const ShelfPage = () => {
  const query = useStaticQuery(graphql`
    query {
      current: allAirtable(
        filter: {
          table: { eq: "Shelf" }
          data: { Stage: { eq: "In progress" } }
        }
      ) {
        edges {
          node {
            data {
              Title
              Author
            }
          }
        }
      }
      completed: allAirtable(
        filter: {
          table: { eq: "Shelf" }
          data: { Stage: { eq: "Completed" }, Rating: { gte: 4 } }
        }
        sort: { fields: id, order: DESC }
      ) {
        edges {
          node {
            data {
              Title
              Author
            }
          }
        }
      }
    }
  `);

  //   const current = query.current.edges.map(edge => edge.node.data);
  const { current, completed } = getAirtableRows(query);

  return (
    <Layout>
      <SEO title="Shelf" />
      <Container className="intro-container" fluid>
        <h1 className="title">Shelf</h1>
        <p>
          An unordered collection of books, music, films, and other media that I
          highly recommend to others. Somewhat inspired by Patrick Collision's{' '}
          <a href="https://patrickcollison.com/bookshelf">bookshelf</a>, except
          of course it's not just limited to books.
        </p>
        <ul>
          {completed.map((book, i) => (
            <li style={{ fontStyle: 'italic' }} key={`completed-${i}`}>
              {book.Title}
            </li>
          ))}
        </ul>
      </Container>
    </Layout>
  );
};

const getAirtableRows = query => {
  const rows = { ...query };
  Object.entries(rows).forEach(
    ([key, value]) => (rows[key] = value.edges.map(edge => edge.node.data))
  );
  return rows;
};

export default ShelfPage;
